@use "./mixins" as *;
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');
@import "./reset";
@import "./variable";

body {
  display: flex;
  min-height: 100vh;
  height: 100%;

  font-family: 'Inter', sans-serif;
  font-size: 16px;

  background-color: #D0CECE;
}
#root {
  width: 100%;
}

input, textarea {
  &:focus, &:active {
    outline: 2px solid #3800EB;
  }
}
